nav {
  background: #eae6f4;
}

.navbar {
  padding: 23px 0;
}

.navbar-brand {
  position: relative;
  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 1px;
    height: 100%;
    background: $black;
    right: -10px;
    top: 0;
  }
}

.nav-link {
  color: $black;
  font-weight: 500;
  font-size: rem(14);
  line-height: 16.002px; /* 114.3% */
  letter-spacing: 1.4px;
}

.active {
  text-decoration: underline;
  text-underline-offset: 5px;
}
