.programmer {
  margin: 180px 0;

  @include phone {
    margin: 80px 0;
  }
}

._border {
  border-width: 5px;
  border-style: dashed;
  border-color: #3a0987;
  padding: 67px 35px 67px 35px;
  text-align: center;

  .btn-yellow {
    margin-top: 60px;
  }

  @include phone {
    padding: 47px 16px 47px 16px;

    .btn-yellow {
      margin-top: 30px;
    }
  }
}

.title {
  font-size: rem(36);
  font-weight: 500;
  line-height: 60px;
}
